"use client";

import React, { useState, useEffect, useRef } from "react";
import ChatContainer from "@/components/chat/ChatContainer";
import ChatMessage from "@/components/chat/ChatMessage";
import ChatInput from "@/components/chat/ChatInput";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";

interface Message {
  role: "user" | "assistant";
  content: string;
  timestamp: string;
}

const ChatPage = () => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const chatContainerRef = useRef<HTMLDivElement>(null);

  // Mensaje de bienvenida inicial
  useEffect(() => {
    const welcomeMessage: Message = {
      role: "assistant",
      content: "Â¡Hola! ðŸŒ¸ Soy Yuki-chan, tu mentora de cÃ³digo personal. Estoy aquÃ­ para ayudarte a aprender programaciÃ³n de manera divertida y fÃ¡cil de entender.\n\nÂ¿En quÃ© te gustarÃ­a que te ayude hoy? Puedo explicarte conceptos, revisar cÃ³digo, sugerir ejercicios, o responder cualquier duda que tengas sobre programaciÃ³n. âœ¨",
      timestamp: new Date().toLocaleTimeString()
    };
    setMessages([welcomeMessage]);
  }, []);

  // Auto-scroll al Ãºltimo mensaje
  useEffect(() => {
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTop = chatContainerRef.current.scrollHeight;
    }
  }, [messages]);

  const handleSend = async (userMessage: string) => {
    const newUserMessage: Message = {
      role: "user",
      content: userMessage,
      timestamp: new Date().toLocaleTimeString()
    };

    setMessages(prev => [...prev, newUserMessage]);
    setLoading(true);
    setError(null);

    try {
      const response = await fetch("/api/waifu-chat", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          messages: [...messages, newUserMessage].map(msg => ({
            role: msg.role,
            content: msg.content
          }))
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || "Error al comunicarse con el servidor");
      }

      const assistantMessage: Message = {
        role: "assistant",
        content: data.message,
        timestamp: new Date().toLocaleTimeString()
      };

      setMessages(prev => [...prev, assistantMessage]);

    } catch (error) {
      console.error("Error en chat:", error);
      const errorMessage = error instanceof Error ? error.message : "Error desconocido";
      
      setError(errorMessage);
      
      const errorResponse: Message = {
        role: "assistant",
        content: `Lo siento, ocurriÃ³ un error: ${errorMessage}\n\nÂ¿PodrÃ­as intentar de nuevo? ðŸ˜…`,
        timestamp: new Date().toLocaleTimeString()
      };

      setMessages(prev => [...prev, errorResponse]);
    } finally {
      setLoading(false);
    }
  };

  const clearChat = () => {
    const welcomeMessage: Message = {
      role: "assistant",
      content: "Â¡Chat reiniciado! ðŸŒ¸ Â¿En quÃ© puedo ayudarte ahora?",
      timestamp: new Date().toLocaleTimeString()
    };
    setMessages([welcomeMessage]);
    setError(null);
  };

  return (
    <div className="min-h-screen flex flex-col bg-gradient-to-br from-pink-50 via-purple-50 to-blue-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b border-pink-100">
        <div className="max-w-4xl mx-auto px-4 py-4 flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-full bg-gradient-to-r from-pink-400 to-purple-400 flex items-center justify-center text-white font-bold text-lg">
              Y
            </div>
            <div>
              <h1 className="text-xl font-bold bg-gradient-to-r from-pink-600 to-purple-600 bg-clip-text text-transparent">
                Waifu GPT
              </h1>
              <p className="text-sm text-gray-600">Tu mentora de cÃ³digo personal</p>
            </div>
          </div>
          <Button 
            onClick={clearChat}
            variant="outline"
            size="sm"
            className="text-gray-600 hover:text-gray-800"
          >
            Limpiar Chat
          </Button>
        </div>
      </header>

      {/* Error Banner */}
      {error && (
        <div className="bg-red-50 border-l-4 border-red-400 p-4 mx-4 mt-4 rounded">
          <div className="flex">
            <div className="ml-3">
              <p className="text-sm text-red-700">
                <strong>Error:</strong> {error}
              </p>
            </div>
          </div>
        </div>
      )}

      {/* Chat Area */}
      <div className="flex-1 flex flex-col max-w-4xl mx-auto w-full">
        <Card className="flex-1 flex flex-col m-4 shadow-lg border-0 bg-white/80 backdrop-blur-sm">
          <div ref={chatContainerRef} className="flex-1 overflow-hidden">
            <ChatContainer>
              {messages.map((msg, index) => (
                <ChatMessage
                  key={index}
                  sender={msg.role === "user" ? "user" : "waifu"}
                  content={msg.content}
                  timestamp={msg.timestamp}
                />
              ))}
              {loading && (
                <ChatMessage
                  sender="waifu"
                  content="Pensando... ðŸ¤”âœ¨"
                  timestamp={new Date().toLocaleTimeString()}
                />
              )}
            </ChatContainer>
          </div>
          <ChatInput 
            onSend={handleSend} 
            disabled={loading}
            placeholder={loading ? "Yuki-chan estÃ¡ pensando..." : "PregÃºntame sobre programaciÃ³n..."}
          />
        </Card>
      </div>

      {/* Footer */}
      <footer className="text-center py-4 text-sm text-gray-500">
        <p>Hecho con ðŸ’– para ayudarte a aprender programaciÃ³n</p>
      </footer>
    </div>
  );
};

export default ChatPage;
